body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*Не забывать побить стили*/
html, body, #root {
  min-height: 100vh;
}

.page {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  min-height: 100vh;
  background-color: rgba(0, 0, 0, .1);
}

.header {
  z-index: 1;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  background-color: green;
  height: 60px;
  font-family: arial;

}

.logo {
  color: white;
  font-style: italic;
  font-size: 20px;
  font-weight: 700;
}

.white {
  text-decoration: none;
  color: white;
}

.medium {
  font-size: 20px;
}

.big {
  font-size: 30px;
  font-weight: 700;
}

.small {
  font-size: 14px;
}

.content {
  -webkit-flex: 1 0 auto;
          flex: 1 0 auto;
}

.footer {
  background-color: blue;
  height: 50px;
  -webkit-flex: 0 0 auto;
          flex: 0 0 auto;
}
.card {
  background-color: #fff;
  height: 150px;
  margin: 30px;
  border-radius: 25px;
}
.nav {
  left: -150px;
  padding-top: 100px;
  position: absolute;
  width: 150px;
  background-color: #fff;
  min-height: 100vh;
  transition: all 500ms;
}
.nav.visible {
  left: 0;
}

